import React, { useState } from "react";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import Grid from "@material-ui/core/Grid";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import WatchLaterIcon from "@material-ui/icons/WatchLater";
import RemoveCircleIcon from "@material-ui/icons/RemoveCircle";
import Brightness1Icon from "@material-ui/icons/Brightness1";
import { green, red, orange, grey } from "@material-ui/core/colors";
import Button from "@material-ui/core/Button";
import CallIcon from "@material-ui/icons/Call";
import { searchUsers } from "../msgraph/searchUsers.js";
import { getPresence } from "../msgraph/getPresence.js";
import CircularProgress from "@material-ui/core/CircularProgress";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import Grow from "@material-ui/core/Grow";
//import { transferCall } from "../genesysCommunication.js";

export default function Search() {
  const [options, setOptions] = useState([]);
  const [inputValue, setInputValue] = React.useState("");
  const [buttonDisable, setButtonDisable] = React.useState(true);
  const [open, setOpen] = React.useState(false);
  const loading = open && options.length === 0 && inputValue.length > 0;

  function renderAvailability(availability) {
    switch (availability) {
      case "Available":
        return (
          <CheckCircleIcon
            fontSize="small"
            style={{ color: green[400], marginRight: "5px" }}
          />
        );
      case "Away" || "BeRightBack":
        return (
          <WatchLaterIcon
            fontSize="small"
            style={{ color: orange[400], marginRight: "5px" }}
          />
        );
      case "DoNotDisturb":
        return (
          <RemoveCircleIcon
            fontSize="small"
            style={{ color: red[400], marginRight: "5px" }}
          />
        );
      case "Busy":
        return (
          <Brightness1Icon
            fontSize="small"
            style={{ color: red[400], marginRight: "5px" }}
          />
        );
      default:
        return (
          <HighlightOffIcon
            fontSize="small"
            style={{ color: grey[400], marginRight: "5px" }}
          />
        );
    }
  }

  React.useEffect(() => {
    let newOptions = [];
    let arrayLenght = 0;

    if (inputValue.length > 0) {
      (async () => {
        let usersResponse = await searchUsers(inputValue);
        if (!usersResponse.error) {
          if (usersResponse.value.length > 10) {
            arrayLenght = 10;
          } else {
            arrayLenght = usersResponse.value.length;
          }

          for (var i = 0; i < arrayLenght; i++) {
            let presenceResponse = await getPresence(usersResponse.value[i].id);
            newOptions.push({
              displayName: usersResponse.value[i].displayName,
              availability: presenceResponse.availability,
              phoneNumber: usersResponse.value[i].mobilePhone
            });
          }
          setOptions(newOptions);
          newOptions = [];
        }
      })();
    } else {
      setOptions(newOptions);
    }
  }, [inputValue, loading]);

  React.useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);

  function handleCall(options){
    console.log(options[0].phoneNumber);
   // transferCall(options[0].phoneNumber);
  }

  return (
    <Grow in={true}>
      <Grid
        width="410"
        container
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
        spacing={1}
      >
        <Grid item xs={9}>
          <Autocomplete
            id="msteams-presense-search"
            width="300"
            autoHighlight
            options={options}
            open={open}
            onOpen={() => {
              setOpen(true);
            }}
            onClose={() => {
              setOpen(false);
            }}
            loading={loading}
            getOptionLabel={(option) => option.displayName}
            onChange={(event, value) => setButtonDisable(!value)}
            onInputChange={(event, newInputValue) => {
              setInputValue(newInputValue);
            }}
            renderInput={(params) => (
              <TextField
                {...params}
                variant="outlined"
                label="Search"
                InputProps={{
                  ...params.InputProps,
                  endAdornment: (
                    <React.Fragment>
                      {loading ? (
                        <CircularProgress color="inherit" size={20} />
                      ) : null}
                      {params.InputProps.endAdornment}
                    </React.Fragment>
                  ),
                }}
              />
            )}
            renderOption={(option) => {
              return (
                <Grid container alignItems="center">
                  {renderAvailability(option.availability)}
                  {option.displayName}
                </Grid>
              );
            }}
          />
        </Grid>
        <Grow
          in={true}
          style={{ transformOrigin: "0 0 0" }}
          {...{ timeout: 1000 }}
        >
          <Grid item xs={3}>
            <Button
              variant="contained"
              color="primary"
              disabled={buttonDisable}
              startIcon={<CallIcon />}
              onClick={() => handleCall(options)}
            >
              Call
            </Button>
          </Grid>
        </Grow>
      </Grid>
    </Grow>
  );
}
